import { OggVorbisDecoder } from './ogg-vorbis-module.js';

const decoders = new Map(); // pane -> state

async function ensureDecoderState(pane) {
    let state = decoders.get(pane);
    if (!state) {
        const decoder = new OggVorbisDecoder();
        if (decoder.ready instanceof Promise) {
            await decoder.ready;
        }
        state = {
            decoder,
            initialized: false,
            samplesAlreadySent: 0,
            sampleRate: 0,
            channelsCount: 0,
        };
        decoders.set(pane, state);
    }
    return state;
}

async function decodeHeader(state) {
    if (!state?.header || !state?.decoder) {
        return;
    }

    const { decoder } = state;

    try {
        // некоторые реализации требуют decode(), не feed()
        const res = await decoder.decode(state.header);
        if (res?.channelData?.length) {
            // Иногда header уже может вернуть пустой PCM, это ок
            state.sampleRate = res.sampleRate || state.sampleRate;
            state.channelsCount = res.channelData.length;
            state.samplesAlreadySent += res.channelData[0].length;
        }
    } catch (err) {
        console.warn('[decoder:init] failed feed header', err);
    }
}

self.onmessage = async (e) => {
    const msg = e.data || {};
    const { id, cmd, data, pane } = msg;

    if (!pane) {
        self.postMessage({ id, error: 'Missing pane id' });
        return;
    }

    try {
        const state = await ensureDecoderState(pane);
        const { decoder } = state;

        // === INIT ===
        if (cmd === 'initDecoder') {
            try {
                // flush сбрасывает остатки PCM, но не всегда всё очищает
                await decoder.flush?.();
                // обнуляем накопленные метаданные
                state.samplesAlreadySent = 0;
                state.sampleRate = 0;
                state.channelsCount = 0;
                state.initialized = false;
            } catch (err) {
                console.warn('[decoder:init] flush/reset failed', err);
            }

            if (data?.oggHeader) {
                state.header = new Uint8Array(data.oggHeader);

                decodeHeader(state);
            }
            state.initialized = true;
            // state.samplesAlreadySent = 0;
            self.postMessage({ id, type: 'initDone', pane });
            return;
        }

        // === PUSH PAGES ===
        if (cmd === 'pushPages') {
            if (!data || !data.file) {
                self.postMessage({ id, error: 'pushPages: no data.file', pane });
                return;
            }


            const chunk = new Uint8Array(data.file);
            let result;
            try {
                result = await decoder.decode(chunk);
            } catch (err) {
                self.postMessage({ id, error: 'decode() failed: ' + err.message, pane });
                return;
            }

            if (!result || !result.channelData?.length) {
                // Ничего нового — просто ждём больше чанков
                return;
            }

            const channels = result.channelData.map(ch =>
                ch instanceof Float32Array ? ch : new Float32Array(ch)
            );
            const sampleRate = result.sampleRate || state.sampleRate || 0;

            // Передаём всё, что вернул decoder — он сам знает, что новое
            state.sampleRate = sampleRate;
            state.channelsCount = channels.length;
            state.samplesAlreadySent += channels[0].length;

            if (channels[0].length) {
                const transfer = channels.map(ch => ch.buffer);
                self.postMessage(
                    { id, pane, channels: channels.map(ch => ch.buffer), sampleRate },
                    transfer
                );
            }
            return;
        }

        // === FLUSH ===
        if (cmd === 'flush') {
            try {
                const result = await decoder.flush();
                if (result?.channelData?.length) {
                    const channels = result.channelData.map(ch =>
                        ch instanceof Float32Array ? ch : new Float32Array(ch)
                    );
                    const sampleRate = result.sampleRate || state.sampleRate || 0;
                    const transfer = channels.map(ch => ch.buffer);

                    await decodeHeader(state);

                    self.postMessage(
                        { id, pane, channels: channels.map(ch => ch.buffer), sampleRate, flushed: true },
                        transfer
                    );
                }
            } catch (err) {
                self.postMessage({ id, pane, error: 'flush() failed: ' + err.message });
            }
            return;
        }

        // === DISPOSE ===
        if (cmd === 'dispose') {
            decoders.delete(pane);
            self.postMessage({ id, type: 'disposed', pane });
            return;
        }

        // === RESET ===
        if (cmd === 'resetAll') {
            decoders.clear();
            self.postMessage({ id, type: 'resetDone' });
            return;
        }

        if (cmd === 'softReset') {
            try {
                // сбрасываем хвост PCM
                await decoder.flush?.();

                // обнуляем только прогресс, но не удаляем decoder
                state.samplesAlreadySent = 0;

                // Не трогаем sampleRate и channelsCount — они нужны
                // чтобы не терять codebooks и формат.

                self.postMessage({ id, pane, type: 'softResetDone' });
            } catch (err) {
                self.postMessage({ id, pane, error: 'softReset failed: ' + err.message });
            }
            return;
        }
    } catch (err) {
        self.postMessage({ id, pane, error: String(err?.message || err) });
    }
};
